import { Component, OnInit, OnChanges } from "@angular/core";
import { ActivatedRoute, NavigationEnd } from "@angular/router";
import { DataServiceService } from "../../data-service.service";
import { NgxSpinnerService } from "ngx-spinner";
import { Router } from "@angular/router";

  import { filter } from "rxjs/operators";
@Component({
  selector: "app-nested-notes",
  templateUrl: "./nested-notes.component.html",
  styleUrls: ["./nested-notes.component.scss"]
})
export class NestedNotesComponent implements OnInit, OnChanges {

  subject_id: string;
  topics: any;
  subject_name:string;
  subject_string;
  tab: number = 1 ;
  constructor(
    private activatedRoute: ActivatedRoute,
    private data_service: DataServiceService,
    private spinner: NgxSpinnerService,
    private router: Router
  ) {
    this.activatedRoute.params.subscribe((params) => {
      console.log(params);
      console.log(this.activatedRoute.snapshot.data);
      switch (params.subject_name) {
        case 'english':
          localStorage.setItem('subject_id', '1');
          console.log('English');
          this.subject_name = 'English';
          this.getTopics('1');
          this.initialLoad();
          break;
        case 'math':
          localStorage.setItem('subject_id', '2');
          console.log('English1');
          this.subject_name = 'Math';
          this.getTopics('2');

          this.initialLoad();
          break;
        case 'science':
          localStorage.setItem('subject_id', '3');
          this.subject_name = 'Science';
          this.getTopics('3');
          this.initialLoad();
          break;
        case 'social':
          localStorage.setItem('subject_id', '4');
          this.subject_name = 'Social Studies';
          this.initialLoad();
          this.getTopics('5');
          break;
        case 'kiswahili':
          localStorage.setItem('subject_id', '5');
          this.subject_name = 'Kiswahili';
          this.getTopics('4');
          this.initialLoad();
          break;
        case 'religion':
          localStorage.setItem('subject_id', '6');
          this.subject_name = 'Religion';
          this.getTopics('6');
          this.initialLoad();
          break;

      }
    });
  }
  ngOnChanges(){

  }
  ngOnInit() {
    this.initialLoad();
    // this.router.events
    //   .pipe(filter(event => event instanceof NavigationEnd))
    //   .subscribe(() => {

    //     this.subject_id = localStorage.getItem('subject_id');
    //     console.log(this.subject_id);
    //     this.initialLoad();
    //   });
  }

  //On first Load loads the active subject grade 1 notes
  initialLoad(){
    this.spinner.show();
    this.subject_id = localStorage.getItem("subject_id");
    this.data_service.getTopics(this.tab, this.subject_id).subscribe(response => {

      console.log(response);
      this.topics = response;
      console.log(this.topics);
      this.spinner.hide();
    });

  }

  //Method that gets topics from active subject on click of grade
  getTopics(class_id: string) {
    var subject_id = "";
    this.activatedRoute.queryParams.subscribe(response => {
      console.log(response);
      subject_id = response.subject_id;
    });
    console.log(subject_id)
    this.data_service.getTopics(class_id, subject_id).subscribe(response => {
      console.log(response);
      this.spinner.hide();
    });
  }

  // Called every time a tab is clicked to load the respective topics
  tabClick(tab) {
    this.spinner.show();
    console.log(tab);
    this.tab = tab;
    var class_id = tab.index + 1;
    console.log(class_id);
    var subject_id = ""
    this.activatedRoute.queryParams.subscribe(response => {
      console.log(response);
      subject_id = response.subject_id;
    });
    this.data_service.getTopics(this.subject_id, class_id).subscribe(
      response => {
        this.topics = response;
        console.log(this.topics);
        this.spinner.hide();
      },
      err => {
        this.spinner.hide();
      }
    );
  }

  //Opens subtopic page based on subject and specified class
  getSubtopics(topic) {
    console.log("Load subtopics Triggered");
    this.spinner.show();
    console.log(this.subject_id);
    localStorage.setItem('topic', topic.id);

    this.router.navigateByUrl("/subtopic/"+ this.subject_name.toLowerCase());

    this.spinner.hide();
  }
}
