import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { RouterModule, Routes } from '@angular/router';
import { NotesComponent } from './notes.component';
// import {BrowserAnimationsModule} from '@angular/platform-browser/animations';
import {MatTabsModule} from '@angular/material/tabs';
import {MatSidenavModule} from '@angular/material/sidenav';
import { FormsModule,FormBuilder } from "@angular/forms";
import { Browser } from 'protractor';
import { TeacherNotesComponent } from './teacher-notes/teacher-notes.component';
import { QuizzesComponent } from './quizzes/quizzes.component';
import { QueryComponent } from './query/query.component';
import { ForumComponent } from './forum/forum.component';
import { NestedNotesComponent } from "./nested-notes/nested-notes.component";

const routes: Routes = [
  {
    path: '',
    component: NotesComponent,
    children: [

      {
        path: ':subject_name',
        component: NestedNotesComponent
      },
      {
        path: 'teacher_notes',
        component: TeacherNotesComponent
      },
      {
        path: 'quizzes',
        component: QuizzesComponent
      },
      {
        path: 'query',
        component: QueryComponent
      },
      {
        path: 'forum',
        component: ForumComponent
      }
    ]
  }
]
@NgModule({
  declarations: [
    NotesComponent,
    TeacherNotesComponent,
    QuizzesComponent,
    QueryComponent,
    ForumComponent,
    NestedNotesComponent

  ],
  imports: [
    CommonModule,
    MatTabsModule,
    MatSidenavModule,
    FormsModule,
    RouterModule.forChild(routes)
  ],
  providers: [FormBuilder]
})
export class NotesModule { }
